import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Label, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, CstCofins } from '../../../../models/dtos/ger/cstCofins';

interface CstCofinsEditViewProps extends EditViewProps<CstCofins> {}

const CSTCOFINSEditView: FC<CstCofinsEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<CstCofins>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de COFINS" wikiId={2544}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 2), required: true } }}
              id="codigo"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código."
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 150 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <Label style={{ marginRight: 20 }}>Utilizar CST para</Label>
            <CustomInput id="checkEntrada" inline={true} label="Entrada" name="entrada" innerRef={formProps.register} type="switch" />
            <CustomInput id="checkSaida" inline={true} label="Saída" name="saida" innerRef={formProps.register} type="switch" />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CSTCOFINSEditView;
